import { ConvertToPluginActionCreators, Selection } from '@ali/4ever-cangjie';

export const CLEAR_STYLES = Symbol('CLEAR_STYLES');
export const CLEAR_PARAGRAPH_STYLE = Symbol('CLEAR_PARAGRAPH_STYLE');

export function clearStyles(ranges?: Selection[]) {
  return {
    type: CLEAR_STYLES,
    payload: {
      ranges,
    },
  };
}

export function clearParagraphStyle(range: Selection) {
  return {
    type: CLEAR_PARAGRAPH_STYLE,
    payload: {
      range,
    },
  };
}

export const actions = {
  clearStyles,
  clearParagraphStyle,
};

/** @deprecated */
export const DEPRECATED_ACTIONS = {
  CLEAR_STYLES,
  CLEAR_PARAGRAPH_STYLE,
  clearStyles,
  clearParagraphStyle,
};

declare module '@ali/4ever-cangjie' {
  interface PluginActionCreators extends ConvertToPluginActionCreators<typeof actions> {}
}
